<template>
  <div>
    <mt-popup
      class="popup-container"
      v-model="popupVisible"
      :closeOnClickModal="false"
      :position="position">
      <p class="popup-content"
         :class="{'border-bottm': (index!==menuLists.length - 1) || isCancelBtnShow,'selected': $route.path === item.path}"
         v-for="(item, index) in menuLists"
         :key="index"
         @click="closePopup(item)">
        {{item.name}}</p>
      <mt-button
        class="cancel-btn"
        v-if="isCancelBtnShow"
        @click="cancelPopop"
        type="primary">
        取消</mt-button>
    </mt-popup>
  </div>
</template>

<script>
export default {
  name: 'MyPopubList',
  data () {
    return {
    }
  },
  props: {
    popupVisible: {
      type: Boolean,
      required: true
    },
    menuLists: {
      type: Array,
      required: false
    },
    position: {
      type: String,
      required: false,
      default: 'bottom'
    },
    isCancelBtnShow: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  computed: {},
  created () {
  },
  methods: {
    // 选择内容后触发父组件的方法
    closePopup (itemParams) {
      let that = this
      that.$emit('select-content', itemParams)
    },
    cancelPopop () {
      let that = this
      that.$emit('select-content')
    }
  }
}
</script>

<style scoped lang="less">
  .mint-popup {
    width: 100%;
    max-height: 60%;
    overflow: auto;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
  }
  .popup-container {
    width: 100%;
    .popup-content {
      font-size: 1rem;
      width: 100%;
      padding: .6rem 0;
    }
    .border-bottm {
      border-bottom: 1px solid #ece5e5;
    }
    .selected {
      background-color: #9cd5de;
    }
    .cancel-btn {
      width: 80%;
      margin: .5rem auto;
    }
  }
</style>



